html {
  height: 100vh;
  width: 100vw;
}
body {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template: auto 1fr auto/ auto;
  margin: 0;
}

/* 버튼 처리 */
.valid {
  border: 1rem solid green;
  background-color: green;
  color: white;
}
.valid:hover {
  opacity: 0.7;
}
.invalid {
  border: 1rem solid red;
  background-color: red;
  color: white;
}
.invalid:hover {
  opacity: 0.7;
}

/* header */
header {
  grid-column: 1/-1;
  background-color: rgb(145, 57, 57);
  color: white;
}
header > h1 {
  padding-left: 1rem;
}
/* dialog */
form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
select option:checked {
  color: rgb(185, 185, 185) !important;
}
select {
  appearance: none;
  color: rgb(185, 185, 185);
  height: 2rem;
  text-align: center;
  background-color: white;
  border-radius: 5px;
}
dialog {
  border-radius: 2rem;
  padding: 2rem 2rem;
}
dialog * {
  margin: 0;
  padding: 0;
}
dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.308);
}
input {
  height: 2rem;
}
menu {
  display: flex;
  justify-content: space-evenly;
}
menu button {
  padding: 5px 15px;
  border-radius: 20px;
  background-color: rgb(184, 111, 111);
}
/* main */
main {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  grid-column: 1/-1;
  display: grid;
  grid-template: repeat(auto-fit, 400px) / repeat(auto-fit, 250px);
  padding: 1rem;
  gap: 2rem;
}
.card {
  background-color: brown;
  color: white;
  height: 400px;
  width: 250px;
  border: 1px solid black;
  border-radius: 1rem;
  border-bottom-right-radius: 0px;
  border-right: 0.2rem solid white;
  border-bottom: 1rem solid white;
}
.newcard {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: stretch;
}
.newcard > *:not(button) {
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.addcard {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4rem;
}
.addcard:hover {
  cursor: pointer;
  background-color: rgb(173, 173, 173);
}

/* footer */
footer {
  bottom: 0;
  color: white;
  grid-row: -2/-1;
  background-color: black;
  display: flex;
  justify-content: center;
}
.formValid {
  border: 1px solid lightgreen;
}
.formInvalid {
  border: 1px solid red;
}
.formValid:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: lightgreen;
}

.formInvalid:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: red;
}
